<template>

    <v-col cols="4">
      <v-card @click="openPhoto">
        <v-card-title>{{ photo.title }}</v-card-title>
        <v-card-text>
          <v-img
              width="200"
              height="200"
              :src="photo.url"
          />
        </v-card-text>
      </v-card>
    </v-col>

</template>

<script>
import {mapMutations} from 'vuex';

export default {
  name: "Photo",
  props: {
    photo: {}
  },
  methods: {
    ...mapMutations(['setCurrentPhoto', 'showDialog']),
    openPhoto() {
      this.setCurrentPhoto(this.photo);
      this.showDialog();
    }
  }
}
</script>

